

* ,*::after, *::before
{
    margin:0;
    padding:0;
    box-sizing: inherit;
}
html
{
    font-size : 62.5%;
    font-family: "Open Sans", sans-serif;
    scroll-behavior: smooth;
    zoom: 1;
}
body {
  font-family: "Open Sans", sans-serif;
  color: #272829;
  overflow-x: hidden;
}

a {
  color: #0563bb;
}

a:hover {
  color: #067ded;
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Raleway", sans-serif;
}
header {
  opacity: 0.8;
}



.nav-menu * {
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav-menu > ul > li {
  position: relative;
  white-space: nowrap;
}

.nav-menu a {
  display: flex;
  align-items: center;
  color: #45505b;
  padding: 10px 18px;
  margin-bottom: 8px;
  transition: 0.3s;
  font-size: 15px;
  border-radius: 50px;
  background: #f2f3f5;
  height: 56px;
  width: 100%;
  overflow: hidden;
  transition: 0.3s;
}

.nav-menu a i {
  font-size: 20px;
}

.nav-menu a span {
  padding: 0 5px 0 7px;
  color: #45505b;
}


  .nav-menu a {
    width: 56px;
  }
  .nav-menu a span {
    display: none;
    color: #fff;
  }

.nav-menu a:hover, .nav-menu .active > a, .nav-menu li:hover > a {
  color: #fff;
  background: #0563bb;
}

.nav-menu a:hover span, .nav-menu .active > a span, .nav-menu li:hover > a span {
  color: #fff;
}

.nav-menu a:hover, .nav-menu li:hover > a {
  width: 100%;
  color: #fff;
  border-bottom: none;
  text-decoration: none;
}

.nav-menu a:hover span, .nav-menu li:hover > a span {
  display: block;
  text-decoration: none;
}
.container-all  {
  position: relative;
  width: 100%;
}
header{
  position: fixed;
  top: 30%;
  left: 1%;
  z-index: 100;
}
#hero {
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  background-size:cover;

}

@media (min-width: 992px) {
  #hero {
    padding-left: 160px;
  }
}

#hero:before {
  content: "";
  background: rgba(255, 255, 255, 0.8);
  position: absolute;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
}



#hero h1 {
  margin: 0;
  font-size: 64px;
  font-weight: 700;
  line-height: 56px;
  color: #45505b;
}

#hero h2{
  margin-top: 8px;

}

#hero p {
  color: #45505b;
  margin: 15px 0 0 0;
  font-size: 26px;
  font-family: "Poppins", sans-serif;
  text-decoration: none;
}

#hero p span {
  color: #0563bb;
  letter-spacing: 1px;
}

#hero .social-links {
  margin-top: 25px;
}

#hero .social-links a {
  font-size: 30px;
  display: inline-block;
  color: #45505b;
  line-height: 1;
  margin-right: 20px;
  transition: 0.3s;
}

#hero .social-links a:hover {
  color: #0563bb;
}

@media (max-width: 992px) {
  #hero {
    text-align: center;
  }
  #hero h1 {
    font-size: 32px;
    line-height: 36px;
  }
  #hero p {
    margin-top: 10px;
    font-size: 20px;
    line-height: 24px;
  }
}

.card {
  transition: all .5s ease;
  text-decoration: none;
  padding: 1.5rem 1rem;
}

.card img {
  height: 170x;
}

.card:hover{
  transform: translateY(-5px);
  box-shadow: 1px 5px 7px 5px rgba(69, 80, 91, 0.66);
}

.card  a {
  font-size: 30px;
  display: flex;
  flex-direction: column;
  color: #45505b;
  line-height: 1;
  margin-right: 20px;
  transition: 0.3s;
  text-decoration: none;
}

@media (max-width: 992px){
  .card-link__1{
    margin-bottom: 10px;
  }
}

@media (max-width: 768px)  {
  .card-deck{
    flex-flow: column wrap !important;
    margin: 0 -25px!important;
  }
  .card-deck .card{
    margin-bottom: 10px!important;
  }
}
